.ProductOverview {
  background: url("../../assets/image1.jpg");
  width: 100%;
  height: 500px;
}
.ProductSection {
  background: url("../../assets/image6.jpg");
  width: 100%;
  height: 600px;
}

.FoodContent {
  width: 100%;
  text-align: center;
}
.Options {
  background-color: white;
  text-align: center;
  padding: 16px;
  margin: 72px 10px;
  height: 500px;
  width: 30%;
  display: inline-block;
  vertical-align: middle;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid black;
}
.CartSection{
  background-color: white;
  text-align: center;
  height: 500px;
  width: 30%;
  display: inline-block;
  vertical-align: middle;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid black;
}
.Cart {
  text-align: center;
  width: 80%;
  height: 450px;
  display: inline-block;
  vertical-align: middle;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.ListItems {
  list-style: none;
  margin: 0;
  padding: 0;
}
.OptionList {
  text-align: left;
  display: block;
  width: 100%;
  margin: 20px 10px;
}
.Image {
  display: inline-block;
  height: 50px;
  width: 15%;
  border-radius: 50%;
  vertical-align: middle;
}
.OptionItemName {
  margin: 10px 20px;
  display: inline-block;
  width: 30%;
  vertical-align: middle;
}
.OptionItemPrice {
  text-align: right;
  right: 0;
  width: 30%;
  display: inline-block;
  vertical-align: middle;
}
.OptionList:hover,
.OptionList:active {
  background: blue;
}

.OptionListItem {
  display: inline-block;
}
.CartList {
  text-align: left;
  display: block;
  width: 100%;
  margin: 20px 10px;
}
.CartList:hover,
.CartList:active {
  background: blue;
}
.CartListItem {
  display: inline-block;
}
.CartItemName {
  margin: 10px 20px;
  display: inline-block;
  vertical-align: middle;
  width: 30%;
}
.CartItemPrice {
  text-align: right;
  right: 0;
  width: 30%;
  display: inline-block;
  vertical-align: middle;
}
.Buttons {
  text-align: center;
  padding: 16px;
  margin: 8px;
  display: inline-block;
  width: 20%;
  vertical-align: middle;
}
.AddtoCartBtn {
  font-size: 12px;
  width: 150px;
  display: block;
  text-align: center;
  background: orange;
  color: white;
  margin: 10px 20px;
  padding: 16px;
  -webkit-box-shadow: 1px 2px 2px black;
          box-shadow: 1px 2px 2px black;
  border-radius: 8px;
}
.RemoveBtn {
  font-size: 12px;
  width: 150px;
  margin: 10px 20px;
  padding: 16px;
  display: block;
  text-align: center;
  background: orange;
  color: white;
  -webkit-box-shadow: 1px 2px 2px black;
          box-shadow: 1px 2px 2px black;
  border-radius: 8px;
}
.EmptyList {
  margin: 50% 10px;
}

.PriceSection{
  background: blue;
  width: 100%;
  display: block;
  height: 50px;
}
.TotalPrice{
  color: white;
  text-align: start;
  margin: 0 10px;
  display: inline-block;
  vertical-align: middle;
  width: 30%;
}
.TotalAmount{
  color: white;
  margin: 0 20px;
  display: inline-block;
  vertical-align: middle;
  width: 10%;
}



